<template>
  <div class="login-container">
    <div class="login-form" v-loading="loading">
      <div class="image-area">
        <img src="@/assets/image/login_illustration.png" alt="" />
      </div>
      <div class="input-area">
        <div class="login-logo">
          <img src="@/assets/image/logo_login.png" alt="" />
        </div>

        <div class="input-group" style="margin-top: 30px;">
          <span class="input-group-label">Login ID*</span>
          <input class="input-group-input" type="text" v-model="username" />
        </div>

        <div class="input-group" style="margin-top: 10px;">
          <span class="input-group-label">Password*</span>
          <input class="input-group-input" type="password" v-model="password" />
        </div>

        <div class="login-button" @click="submitLogin">
          Login
        </div>

        <p class="social-buttons">
          or Sign In with Google <br />
          <button @click="socialLogin" class="social-button">
            <img alt="Google Logo" src="@/assets/image/google-logo.png" />
          </button>
        </p>
      </div>

      <div class="external-link-area">
        <span class="external-link">Privacy Policy</span>
        <span class="external-link" style="margin-left:45px;"
          >Term & Conditions</span
        >
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import axios from "axios";
import { SERVER_URL } from "../config";
export default {
  data() {
    return {
      username: "",
      password: "",
      loading: false,
    };
  },
  methods: {
    async submitLogin() {
      this.loading = true;
      const url = SERVER_URL + "/users/getEmailByUserName";
      const payload = {
        username: this.username,
      };
      try {
        const response = await axios.post(url, payload);
        const email = response.data.email;

        // Sign-in to Firebase
        firebase
          .auth()
          .signInWithEmailAndPassword(email, this.password)
          .then(
            (response) => {
              this.loading = false;
              if (!response.user.emailVerified) {
                this.$notify.error({
                  title: "Email isn't verified",
                  message: "Please check your inbox and confirm invite link.",
                });
              } else {
                this.$router.replace("users");
              }
            },
            (err) => {
              this.$notify.error({
                title: "Failed to login",
                message: err.message,
              });
              this.loading = false;
            }
          );
      } catch (error) {
        console.log(error);
        this.$notify.error({
          title: "Failed to Login",
          message: "Username doesn't exist.",
        });
        this.loading = false;
      }
    },
    socialLogin() {
      const provider = new firebase.auth.GoogleAuthProvider();

      firebase
        .auth()
        .signInWithPopup(provider)
        .then((result) => {
          console.log(result);
          const { email } = result.user;

          // Check if email exist in the DB
          const url = SERVER_URL + "/users/getUserByEmail";
          const payload = { email: email };
          axios
            .post(url, payload)
            .then((response) => {
              console.log(response);
              const { result } = response.data;
              if (result > 0) {
                this.$router.replace("users");
              } else {
                this.$notify.error({
                  title: "Failed to Login",
                  message: "Your account isn't regietered.",
                });
                firebase.auth().signOut();
              }
            })
            .catch((err) => {
              firebase.auth().signOut();
              this.$notify.error({
                title: "Failed to login",
                message: err.message,
              });
            });
        })
        .catch((err) => {
          this.$notify.error({
            title: "Failed to login",
            message: err.message,
          });
        });
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/login.scss";
</style>
